@keyframes animation-letters {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes animation-angle-up-down {
  0% {
    transform: translateY(-5%);
  }
  50% {
    transform: translateY(5%);
  }
  100% {
    transform: translateY(-5%);
  }
}

@keyframes animation-angle-left {
  0% {
    transform: translateX(-5%);
  }
  50% {
    transform: translateX(5%);
  }
  100% {
    transform: translateX(-5%);
  }
}

/*----------------------------
 Scroll Animation.
----------------------------*/
.txt-copies[data-emergence=hidden] {
  opacity: 0;
  transform: translateY(50%);
  transition: all 0.75s linear;
}

.txt-copies[data-emergence=visible] {
  opacity: 1;
  transform: translateY(0%);
  transition: all 0.75s ease-out;
}

[class*="txt-copy0"][data-emergence=hidden] {
  opacity: 0;
  transform: translateY(50%);
  transition: all 0.75s linear;
}

[class*="txt-copy0"][data-emergence=visible] {
  opacity: 1;
  transform: translateY(0%);
}

.txt-copy01 {
  transition: all 0.75s 0.5s ease-out;
}

.txt-copy02 {
  transition: all 0.75s 0.75s ease-out;
}

.txt-copy03 {
  transition: all 0.75s 1s ease-out;
}

.txt-copy04 {
  transition: all 0.75s 1.25s ease-out;
}

.txt-copy05 {
  transition: all 0.75s 1.5s ease-out;
}

.movie01-wrapper[data-emergence=hidden] {
  opacity: 0;
  transform: translateY(10%);
  transition: all 0.75s linear;
}

.movie01-wrapper[data-emergence=visible] {
  opacity: 1;
  transform: translateY(0%);
  transition: all 1s ease-out;
}

.wp-wrapper[data-emergence=hidden] {
  opacity: 0;
  transform: translateY(10%);
  transition: all 0.75s linear;
}

.wp-wrapper[data-emergence=visible] {
  opacity: 1;
